import {IEntregavelOrdemServico, IOrdemServico, StatusOrdemServico} from '../../interface-models';
import {getStatusOrdemServico} from '../../interface-models/getStatusOrdemServico';
import {ConstrutorRetornoPermissoes} from '../construirRetorno';
import {RetornoPermisao} from '../RetornoPermisao';
import {tem} from '../tem';
import {TipoUsoPermissoes} from '../TipoUsoPermissoes';

export function editar(
    c: ConstrutorRetornoPermissoes,
    entregavel: IEntregavelOrdemServico,
    ordemServico: IOrdemServico,
): RetornoPermisao {
    /* ############### OU A ORDEM DE SERVIO AINDA EST EM RASCUNHO OU  UM ENTREGVEL NO PREVISTO NA EMISSO DA OS ######*/
    let r = c.construir(
        getStatusOrdemServico(ordemServico) == StatusOrdemServico.RASCUNHO || !tem(entregavel.id), //TODO: pensar sobre atributo que indica incluso na OS na hora da emisso. Deste jeito, vai impedir edio
        'descricao',
        `Descrio do Entregvel no pode ser modificada aps emisso da Ordem de Servio`,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    return r;
}
