import {
    getStatusOrdemServico,
    IEtapaOrdemServico,
    IOrdemServico,
    ITipoOrdemServicoContrato,
    StatusOrdemServico,
} from '../../interface-models';
import {ConstrutorRetornoPermissoes} from '../construirRetorno';
import {RetornoPermisao} from '../RetornoPermisao';
import {tem} from '../tem';
import {TipoUsoPermissoes} from '../TipoUsoPermissoes';

export function emitirTermoAceitacaoSEI(
    c: ConstrutorRetornoPermissoes,
    etapa: IEtapaOrdemServico,
    ordemServico: IOrdemServico,
    tipoOrdemServico: ITipoOrdemServicoContrato,
): RetornoPermisao {
    /* ################## A ETAPA J TEM QUE TER SIDO SALVA NA BASE (TER UM ID) ##############################*/
    let r = c.construir(tem(etapa.id), '', `Etapa no possui um ID`);
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* ################## O TIPO DA OS DEVE PREVER TERMOS DE ACEITAO POR ETAPA ##############################*/
    r = c.construir(
        tipoOrdemServico.termoAceitacaoEmitidoPorEtapa,
        '',
        `O Tipo da Ordem de Servio no est configurado para emisso de termos de aceitao por etapa`,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* ############################# TERMO NO PODE J TER SIDO EMITIDO #####################################*/
    r = c.construir(
        !tem(etapa.numeroDocumentoTermoAceitacaoSEI) && !tem(etapa.linkTermoAceitacaoSEI),
        '',
        `Termo de Aceitao j foi emitido para esta etapa. SEI: ${etapa.numeroDocumentoTermoAceitacaoSEI} - ${etapa.linkTermoAceitacaoSEI}`,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* #################### ORDEM DE SERVIO NO PODE ESTAR EM RASCUNHO #########################*/
    r = c.construir(
        getStatusOrdemServico(ordemServico) > StatusOrdemServico.RASCUNHO,
        '',
        `Termo de Aceitao no pode ser emitido para uma Ordem de Servio em rascunho`,
        r,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* #################### ETAPA NO PODE TER SIDO CANCELADA ##########################*/
    r = c.construir(
        !tem(etapa.dtCancelamento),
        '',
        `A Etapa consta como cancelada. No  possvel emitir Termo de Aceitao`,
        r,
    );
    return r;
}
