import {IEtapaOrdemServico, IOrdemServico, ITipoOrdemServicoContrato} from '../../interface-models';
import {ConstrutorRetornoPermissoesFactory} from '../construirRetorno';
import {RetornoPermisao} from '../RetornoPermisao';
import {TipoUsoPermissoes} from '../TipoUsoPermissoes';
import {editarDescricao} from './editarDescricao';
import {editarPlanejamento} from './editarPlanejamento';
import {editarRealizado} from './editarRealizado';
import {emitirTermoAceitacaoSEI} from './emitirTermoAceitacaoSEI';
import {irParaTermoAceitacaoSEI} from './irParaTermoAceitacaoSEI';
import {remover} from './remover';
import {salvar} from './salvar';

/**
 * Retorna objeto com um funo para cada ao possvel para uma Etapa de Ordem de Servio
 * Essas funes avaliam se, de acordo com o status da {etapaOrdemServico}, a ao est habilitada
 * e retorna um objeto com as propriedades 'ok' e um 'mensagensAtributo', que acumula
 * mensagens de retorno agrupadas pelo nome do atributo, 'ok' = TRUE indica se est habilitada a funo.
 *
 * @param etapa  Etapa de Ordem de servio analisada
 * @param ordemServico  Ordem de servio analisada
 * @param tipoUso Indica o tipo de uso para o qual se est obtendo as permisses. Se VALIDAR_SERVIDOR, lana exceo
 * na primeira no conformidade; se VALIDAR_UI, faz todas as validaes e retorna todas as mensagens de inconformidade;
 * se HABILITAR_UI, retorna {'ok': false} na primeira inconformidade
 */
export function getAcoesEtapaOrdemServico(
    tipoUso: TipoUsoPermissoes,
    etapa: IEtapaOrdemServico,
    ordemServico?: IOrdemServico,
    tipoOrdemServico?: ITipoOrdemServicoContrato,
) {
    const c = ConstrutorRetornoPermissoesFactory(tipoUso);
    return {
        editarDescricao: () => {
            if (!ordemServico)
                throw new Error(
                    `Parmetro 'ordemServico' precisa ser informado para verificar permiso 'editarDescricao'`,
                );
            return editarDescricao(c, etapa, ordemServico);
        },
        editarPlanejamento: () => {
            if (!ordemServico)
                throw new Error(
                    `Parmetro 'ordemServico' precisa ser informado para verificar permiso 'editarPlanejamento'`,
                );
            return editarPlanejamento(c, etapa, ordemServico);
        },
        editarRealizado: () => {
            if (!ordemServico)
                throw new Error(
                    `Parmetro 'ordemServico' precisa ser informado para verificar permiso 'editarRealizado'`,
                );
            return editarRealizado(c, etapa, ordemServico);
        },
        emitirTermoAceitacaoSEI: () => {
            if (!ordemServico)
                throw new Error(
                    `Parmetro 'ordemServico' precisa ser informado para verificar permiso 'emitirTermoAceitacaoSEI'`,
                );
            if (!tipoOrdemServico)
                throw new Error(
                    `Parmetro 'tipoOrdemServico' precisa ser informado para verificar permiso 'emitirTermoAceitacaoSEI'`,
                );
            return emitirTermoAceitacaoSEI(c, etapa, ordemServico, tipoOrdemServico);
        },
        irParaTermoAceitacaoSEI: () => {
            return irParaTermoAceitacaoSEI(c, etapa);
        },
        remover: (): RetornoPermisao => {
            if (!ordemServico)
                throw new Error(`Parmetro 'ordemServico' precisa ser informado para verificar permiso 'remover'`);
            return remover(c, etapa, ordemServico);
        },
        salvar: (): RetornoPermisao => {
            if (!ordemServico)
                throw new Error(`Parmetro 'ordemServico' precisa ser informado para verificar permiso 'salvar'`);
            return salvar(c, etapa, ordemServico);
        },
    };
}
