import {getStatusOrdemServico, IOrdemServico, StatusOrdemServico} from '../../interface-models';
import {ConstrutorRetornoPermissoes} from '../construirRetorno';
import {RetornoPermisao} from '../RetornoPermisao';
import {tem} from '../tem';
import {TipoUsoPermissoes} from '../TipoUsoPermissoes';

export function emitirSEI(c: ConstrutorRetornoPermissoes, ordemServico: IOrdemServico): RetornoPermisao {
    /* ################## A OS J TEM QUE TER SIDO SALVA NA BASE (TER UM ID) ##############################*/
    let r = c.construir(tem(ordemServico.id), '', `Ordem de Servio no possui um ID`);
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* ############################# OS NO PODE J TER SIDO EMITIDA #####################################*/
    r = c.construir(
        !tem(ordemServico.numeroDocumentoOrdemServicoSEI) && !tem(ordemServico.linkOrdemServicoSEI),
        '',
        `Ordem de Servio j foi emitida. SEI: ${ordemServico.numeroDocumentoOrdemServicoSEI} - ${ordemServico.linkOrdemServicoSEI}`,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* #################### ORDEM DE SERVIO DEVE ESTAR EM RASCUNHO #########################*/
    r = c.construir(
        getStatusOrdemServico(ordemServico) == StatusOrdemServico.RASCUNHO,
        '',
        `Ordem de Servio deve estar no modo rascunho para ser emitida no SEI`,
        r,
    );
    //Se o uso for HABILITAR_UI, retorna sem acumular mensagens ou qualquer outra validao/processamento
    if (r.ok == false && c.tipoUso == TipoUsoPermissoes.HABILITAR_UI) return r;

    /* #################### ORDEM DE SERVIO NO PODE TER SIDO CANCELADA ##########################*/
    r = c.construir(
        !tem(ordemServico.dtCancelamento),
        '',
        `A Ordem de Servio consta como cancelada. No  possvel emiti-la no SEI`,
        r,
    );
    return r;
}
